import chroma from "chroma-js";
var isChromaColor = function (color) {
    if (chroma.valid(color)) {
        return true;
    }
    return false;
};
export function colorToHex(color) {
    var value = color.value;
    if (isChromaColor(value)) {
        return chroma(value).hex();
    }
    return "";
}
